'use strict';var atfSlider=document.querySelector('.atf-glide');var options={type:'carousel',startAt:0,perView:1,gap:0,autoplay:4000,animationDuration:600};if(atfSlider){var slide=new Glide(atfSlider,options);slide.mount();}
'use strict';var doctorContainer=document.querySelector('.doctor-main-content');if(doctorContainer){var modals=doctorContainer.querySelectorAll('.doctor-profile-container');var closeModal=doctorContainer.querySelectorAll('.close-modal');var body=document.querySelector('body');modals.forEach(function(m){return m.addEventListener('click',function(e){e.preventDefault();var dataId=m.dataset.doctorId;var modal=document.querySelector('.doctor-modal.modal-'+dataId);modal.classList.add('opened');body.classList.add('no-scroll');setTimeout(function(){modal.classList.add('animation');},100);});});closeModal.forEach(function(close){return close.addEventListener('click',function(e){e.preventDefault();var dataId=close.dataset.modalClose;var modal=document.querySelector('.doctor-modal.modal-'+dataId);modal.classList.remove('animation');setTimeout(function(){modal.classList.remove('opened');body.classList.remove('no-scroll');},300);});});}
'use strict';// Initialize and add the map
var mapContainer=document.querySelector('#map');function initMap(){if(mapContainer){// The location of Uluru
var uluru={lat:13.5900749,lng:124.1933298};// The map, centered at Uluru
var map=new google.maps.Map(mapContainer,{zoom:15,center:uluru});// The marker, positioned at Uluru
var marker=new google.maps.Marker({position:uluru,map:map});}}
'use strict';var serviceContainer=document.querySelector('.services-section-container');if(serviceContainer){var services=serviceContainer.querySelectorAll('.service-title');var closeBtns=serviceContainer.querySelectorAll('.close-modal');var body=document.querySelector('body');services.forEach(function(service){return service.addEventListener('click',function(e){e.preventDefault();var dataset=e.target.dataset.serviceModal;var modal=serviceContainer.querySelector('.modal-'+dataset);body.classList.add('no-scroll');modal.classList.add('opened');setTimeout(function(){modal.classList.add('animation');},100);});});closeBtns.forEach(function(btn){return btn.addEventListener('click',function(e){e.preventDefault();var dataset=e.target.dataset.modalClose;var modal=serviceContainer.querySelector('.modal-'+dataset);modal.classList.remove('animation');setTimeout(function(){modal.classList.remove('opened');body.classList.remove('no-scroll');},300);});});}
'use strict';var serviceSection=document.querySelector('.services-section-container');if(serviceSection){var serviceTabs=serviceSection.querySelectorAll('.services-tabs-btn');var serviceContainers=serviceSection.querySelectorAll('.service-container');var pageAddress=window.location.href;var currentPage=window.location.origin+'/our-services/#';var address=pageAddress.replace(currentPage,'');if(address=="resource-center"||address=="community-program"){var activeBtn=serviceSection.querySelector('button#'+address);setTimeout(function(){activeBtn.click();},300);}serviceTabs.forEach(function(btn){return btn.addEventListener('click',function(e){e.preventDefault();var cat=btn.dataset.catBtn;var service=serviceSection.querySelector('.service-container[data-category="'+cat+'"]');serviceTabs.forEach(function(item){return item.classList.remove('active');});btn.classList.add('active');serviceContainers.forEach(function(container){return container.classList.remove('opened');});service.classList.add('opened');});});}
'use strict';var sideBtn=document.querySelector('.burger-btn');var body=document.querySelector('body');var sidebar=document.querySelector('.mobile-sidebar-navigation');var sidebarOverlay=sidebar.querySelector('.bg-overlay');var closeSidebar=sidebar.querySelector('.close-sidebar');function toggleSideBar(e){e.preventDefault();if(sidebar.classList.contains('opened')){body.classList.remove('no-scroll');sidebar.classList.toggle('opened');setTimeout(function(){sidebar.classList.toggle('animation');},300);}else{body.classList.add('no-scroll');sidebar.classList.toggle('animation');setTimeout(function(){sidebar.classList.toggle('opened');},100);}}sideBtn.addEventListener('click',toggleSideBar);sidebarOverlay.addEventListener('click',toggleSideBar);closeSidebar.addEventListener('click',toggleSideBar);window.addEventListener('resize',function(e){if(window.innerWidth>=768){sidebar.classList.remove('opened');sidebar.classList.remove('animation');}});
'use strict';$(document).ready(function(){var doctors=document.querySelectorAll('.schedule-container');doctors.forEach(function(doctor){return doctor.addEventListener('click',function(e){e.preventDefault();var hdr=acf.getField('header','option');alert(hdr['address']);});});});